 tareas organizadas de manera secuencial que muestran claramente el orden de lo que ocurre en el proceso y su descripción. Casi siempre tiene pocos detalles y muestra cómo suceden las cosas cuando todo funciona correctamente, omitiendo alternativas. Este tipo de diagrama se elabora determinando las entradas y salidas del proceso, y luego de ello, se establece cuál es la actividad inicial y se continúa la secuencia hasta llegar al final del proceso.")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"tarjeta-flip__contenido p-4 p-xl-5"},[_c('h4',{staticClass:"text-center mt-5"},[_vm._v("Diagrama de flujo")]),_c('p',[_vm._v("A diferencia del anterior, este diagrama muestra un mayor grado de detalle, lo que permite visualizar los pasos del proceso, organizar el trabajo y tomar decisiones de importancia. Lo anterior aporta la información mínima para entender el comportamiento del proceso y poder planear adecuadamente las etapas posteriores de análisis con mayor detalle. Este tipo de diagrama se elabora determinando las entradas y salidas del proceso y estableciendo cuál es la actividad inicial, para luego describir, con los símbolos adecuados, las actividades y el flujo del proceso.")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"tarjeta-flip__contenido p-4 p-xl-5"},[_c('h4',{staticClass:"text-center mt-5"},[_vm._v("Diagrama de flujo y participantes")]),_c('p',[_vm._v("Describe el flujo de actividades y sus secuencias, teniendo en cuenta a los participantes en los procesos. Queda escrito con mayor detalle que los anteriores diagramas y es el indicado para documentar la situación actual de los procesos. Para realizarlo, se deben determinar las entradas y salidas del proceso y establecer los responsables de estos. Posteriormente, se establece la primera actividad, con sus responsables, y se sigue estableciendo la secuencia, hasta finalizar el proceso. ")])])}]

